import SelectedPlayers from './SelectedPlayers';
import { useState, useEffect } from 'react';
import styles from './styles/CreateTournament.module.css';

const CreateTournament = () => {
  const [arrayOfPlay, setArrayOfPlay] = useState([]);
  useEffect(() => {
    fetch('http://localhost:5000/api/players')
      .then((response) => response.json())
      .then((data) => setArrayOfPlay(data))
      .then(() => console.log(arrayOfPlay));
  }, []);
  // const [userName, setUsername] = useState('');
  // const [password, setPassword] = useState('');

  const [data, setData] = useState({
    title: '',
    mainReferee: '',
    mainSecretary: '',
    country: '',
    region: '',
    city: '',
    tournamentSystem: '',
    startDate: '',
    endDate: '',
    playersIDs: [],
    referees: [],
    rounds: '',
  });
  const [player, setPlayer] = useState('');
  const [playersssss, setPlayersssss] = useState(''); //  id   

  function handleFormSubmit(event) {
    event.preventDefault();
    // setData([...data.playersIDs, ...playersssss]);
    // console.log(playersssss);
    console.log(data.playersIDs);

    // const userData = {
    //   username: userName,
    //   password: password,
    // };
    // console.log(userData);
    console.log(JSON.stringify(data));

    fetch('http://localhost:5000/api/tournaments', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json;charset=utf-8',
      },
      body: JSON.stringify(data),
    })
      .then((response) =>
        response.ok ? response.json() : Promise.reject(response)
      )
      .then((data) => console.log(data))
      .catch((error) =>
        error.json().then((errorData) => console.error(errorData))
      );
  }
  return (
    <div className="qwert">
      <h1> </h1>
      <form className="blockInput" onSubmit={handleFormSubmit}>
        <label className={styles.labelText} htmlFor="username">
            :
        </label>
        <br></br>
        <input
          placeholder=" "
          type="text"
          value={data.title}
          name="username"
          id="username"
          // onChange={(e) => setUsername(e.target.value)}
          onChange={(e) => setData({ ...data, title: e.target.value })}
          className={styles.inputText}
        />
        <br></br>
        <label className={styles.labelText} htmlFor="referi">
             :
        </label>
        <br></br>
        <input
          placeholder="  "
          type="text"
          value={data.mainReferee}
          name="password"
          id="referi"
          onChange={(e) => setData({ ...data, mainReferee: e.target.value })}
          className={styles.inputText}
        />
        <br></br>
        <label className={styles.labelText} htmlFor="secretary">
             :
        </label>
        <br></br>
        <input
          placeholder="  "
          type="text"
          value={data.mainSecretary}
          name="password"
          id="secretary"
          onChange={(e) => setData({ ...data, mainSecretary: e.target.value })}
          className={styles.inputText}
        />
        <br></br>
        <label className={styles.labelText} htmlFor="country">
            :
        </label>
        <br></br>
        <input
          placeholder=""
          type="text"
          value={data.country}
          name="username"
          id="country"
          // onChange={(e) => setUsername(e.target.value)}
          onChange={(e) => setData({ ...data, country: e.target.value })}
          className={styles.inputText}
        />
        <br></br>
        <label className={styles.labelText} htmlFor="region">
            :
        </label>
        <br></br>
        <input
          placeholder=""
          type="text"
          value={data.region}
          name="username"
          id="region"
          // onChange={(e) => setUsername(e.target.value)}
          onChange={(e) => setData({ ...data, region: e.target.value })}
          className={styles.inputText}
        />
        <br></br>
        <label className={styles.labelText} htmlFor="city">
            :
        </label>
        <br></br>
        <input
          placeholder=""
          type="text"
          value={data.city}
          name="username"
          id="city"
          // onChange={(e) => setUsername(e.target.value)}
          onChange={(e) => setData({ ...data, city: e.target.value })}
          className={styles.inputText}
        />
        <br></br>
        <label className={styles.labelText} htmlFor="select">
            :
        </label>
        <br></br>
        <select
          name="changeType"
          id="select"
          className={styles.inputText}
          onChange={(e) =>
            setData({ ...data, tournamentSystem: e.target.value })
          }
        >
          <option value="">  </option>
          <option value=""> </option>
          <option value=""> </option>
        </select>
        <br></br>
        {/* {data.tournamentSystem == 'swiss' ? (
          <>
            <label htmlFor="rounds" className={styles.labelText}>
                
            </label>
            <br></br>
            <input
              placeholder="5"
              type="number"
              value={data.rounds}
              name="username"
              id="rounds"
              // onChange={(e) => setUsername(e.target.value)}
              onChange={(e) => setData({ ...data, rounds: e.target.value })}
              className={styles.inputText}
            />
          </>
        ) : (
          ''
        )} */}
        <br></br>
        <label className={styles.labelText} htmlFor="startDate">
             :
        </label>
        <br></br>
        <input
          type="date"
          // value={password}
          // name="password"
          id="startDate"
          onChange={(e) => setData({ ...data, startDate: e.target.value })}
          className={styles.inputText}
        />
        <br></br>
        <label className={styles.labelText} htmlFor="endDate">
             :
        </label>
        <br></br>
        <input
          type="date"
          // value={password}
          // name="password"
          id="endDate"
          onChange={(e) => setData({ ...data, endDate: e.target.value })}
          className={styles.inputText}
        />
        <br></br>
        {/* <label className={styles.labelText} htmlFor="dateOfEnd">
           :
        </label>
        <br></br>
        <input
          placeholder=" id     "
          type="number"
          value={player}
          // name="password"
          id="dateOfEnd"
          onChange={(e) => setPlayer(e.target.value)}
          className={styles.inputText}
        /> */}

        <label className={styles.labelText} htmlFor="playersssss">
           :
        </label>
        <br></br>
        <select
          name="changeType"
          id="playersssss"
          className={styles.inputText}
          onChange={(e) =>
            setData({
              ...data,
              playersIDs: [...data.playersIDs, e.target.value],
            })
          }
        >
          <option value=""> </option>
          {arrayOfPlay.map((objOfPlay) => {
            return (
              <option key={objOfPlay['_id']} value={objOfPlay['_id']}>
                {`${objOfPlay['lastName']} ${objOfPlay['firstName']} ${objOfPlay['middleName']} ${objOfPlay['birthday']}`}
              </option>
            );
          })}
        </select>
        <br></br>
        {data.playersIDs.length > 0
          ? data.playersIDs.map((id) => {
              let searchObj = arrayOfPlay.find((play) => play['_id'] === id);
              return (
                <div
                  key={id}
                >{`${searchObj['lastName']} ${searchObj['firstName']} ${searchObj['middleName']} ${searchObj['birthday']}`}</div>
              );
            })
          : ''}
        {/* <br></br> */}
        {/* <div
          onClick={() => {
            setData({
              ...data,
              playersIDs: [...data.playersIDs, player],
            });
            setPlayer('');
          }}
          className={styles.divOfPlayer}
        >
          
        </div>
        <br></br>
        {data.playersIDs.map((id) => {
          return <div key={id}>{id}</div>;
        })} */}
        {/* <SelectedPlayers array={data.playersIDs} /> */}
        {/* <br></br> */}
        {/* <label className={styles.labelText} htmlFor="playersssss">
           :
        </label>
        <br></br>
        <select
          name="changeType"
          id="playersssss"
          className={styles.inputText}
          onChange={(e) => setPlayersssss([...playersssss, e.target.value])}
        >
          <option value=""> </option>
          {arrayOfPlay.map((objOfPlay) => {
            return (
              <option key={objOfPlay['_id']} value={objOfPlay['_id']}>
                {`${objOfPlay['lastName']} ${objOfPlay['firstName']} ${objOfPlay['middleName']} ${objOfPlay['birthday']}`}
              </option>
            );
          })}
        </select>
        <br></br>
        {playersssss.length > 0
          ? playersssss.map((id) => {
              let searchObj = arrayOfPlay.find((play) => play['_id'] === id);
              return (
                <div
                  key={id}
                >{`${searchObj['lastName']} ${searchObj['firstName']} ${searchObj['middleName']} ${searchObj['birthday']}`}</div>
              );
            })
          : ''} */}
        {/* {console.log(playersssss)} */}
        {/* <br></br> */}
        <button className={styles.createButton} type="submit">
           
        </button>
      </form>
    </div>
  );
};

export default CreateTournament;

