import React from 'react';
import TRow from './TRow';

function TBody(props) {
  // console.log(props.rows);
  // props.rows.map((row, ndx) => {
  //   console.log(row);
  //   console.log(ndx);
  // });
  return (
    <tbody>
      {props.rows.map(
        (
          row,
          ndx // row- , ndx-,     
        ) => (
          <TRow
            rowIndex={ndx}
            theme={props.theme}
            // key={row.}
            key={ndx}
            customRenderer={props.customRenderer}
            columns={props.columns}
            row={row}
          ></TRow>
        )
      )}
    </tbody>
  );
}

export default TBody;
