import { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';

const AllTournaments = () => {
  const [arrayTournaments, setArrayTournaments] = useState([]);
  useEffect(() => {
    fetch(`http://localhost:5000/api/tournaments?page=1&limit=10`)
      .then((data) => data.json())
      .then((data) => setArrayTournaments(data));
  }, []);
  console.log(arrayTournaments);

  return (
    <div>
      {arrayTournaments.map((obj) => {
        return (
          <Link to={obj['_id']} key={obj['_id']}>
            {obj.title}
          </Link>
        );
      })}
    </div>
  );
  // return <h1>AllTournaments</h1>;
};

export default AllTournaments;
