import { useState, useEffect } from 'react';
import { Link, useNavigate, useParams } from 'react-router-dom';
import ImageProfile from './ImageProfile';
import AllData from './AllData';
import styles from './CardOfPlayer.module.css';
import Cchart from './Cchart';

const ProfileOfPlayer = () => {
  const [profileData, setProfileData] = useState({});
  const params = useParams();
  // console.log(params);
  useEffect(() => {
    fetch(`http://localhost:5000/api/players/${params['playerSlug']}`)
      .then((response) => response.json())
      .then((data) => setProfileData(data));
  }, []);
  // console.log('pd', profileData);
  const IPlayer = {
    firstName: '', //
    middleName: '', //
    lastName: '', //
    gender: '', //
    birthday: '27.02.1995', // 
    region: '', // 
    sportsCategoryID: '', //ID  
    playerStatsIDs: ' -', // ID    
    sportsCategoryAbbr: '', //  
    sportsOrganization: '', // 
    currentAdamovichRank: '785', //  
    previousAdamovichRank: '700', //  
  };
  const arr = [
    '05.05.2022 1 ',
    '07.05.2022 1 ',
    '09.05.2022 1 ',
    '05.05.2022 1 ',
    '07.05.2022 1 ',
    '09.05.2022 1 ',
  ];
  return (
    <div className={styles.outer}>
      <ImageProfile />
      <AllData dataPlayer={profileData} />
      <Cchart arr={arr} />
    </div>
  );
};

export default ProfileOfPlayer;

